import { transitionTimestamp } from "@/utils/parseTime";
import Alert from "@/components/alert/alert";
export default {
  name: 'MarkdownDemo',
  components: {
    Alert: Alert,
    MarkdownPro: MarkdownPro
  },
  data: function data() {
    return {
      t: "",
      tags: [],
      alltags: [],
      dialog: false,
      options: option,
      article: {},
      draft: {},
      languageTypeList: {
        'en': 'en_US',
        'zh': 'zh_CN',
        'es': 'es_ES'
      }
    };
  },
  mounted: function mounted() {
    var _this = this;

    var path = window.location.href.split("/").splice(-1)[0];
    getAllType().then(function (res) {
      _this.alltags = res.data;
    });

    if (path === "markdown") {
      return;
    }

    var did = window.location.href.split("/").splice(-1)[0];

    if (did.length < 10) {
      getArticleById(did).then(function (res) {
        if (res.data.type === "error") {
          _this.$router.push("/404");
        }

        _this.draft = {
          dtext: res.data.aText,
          dtitle: res.data.aTitle
        };
        _this.article = res.data;

        if (_this.article.aType !== undefined && _this.article.aType !== null) {
          _this.tags = _this.article.aType.split("|");
        }
      });
    } else {
      getDraftById(did).then(function (res) {
        if (res.data.type === "error") {
          _this.$router.push("/404");
        }

        var data = res.data.draft;
        _this.draft = data;
      });
    }
  },
