          });

          if (res.data.type === "success") {
            deleteDraft(_this2.draft.did);

            _this2.$router.push("/");
          }
        });
      } else {
        saveArticle(this.article).then(function (res) {
          _this2.$notify({
            "type": res.data.type,
            "message": res.data.msg
          });

          if (res.data.type === "success") {
            deleteDraft(_this2.draft.did);

            _this2.$router.push("/");
          }
        });
      }
    },
    saveDrafts: function saveDrafts() {
      var _this3 = this;

      var title = this.draft.dtitle;
      var text = this.draft.dtext;
      var html = this.$refs.markdownEditor.html;
      this.draft["dtitle"] = title;
      this.draft['dtext'] = text;
      this.draft["dhtml"] = html;

      if (title === undefined || title.length < 5) {
        this.$notify.error({
          message: "标题不能低于五个字符"
        });
        return;
      }

      _saveDrafts(this.draft).then(function (res) {
        _this3.$notify({
          type: res.data.type,
          message: res.data.msg
        });

        _this3.draft = res.data.draft;
      });
    }
  }
};